
FCBH.docReady(() => {
    FCBH.deleteFromStorage(FCBH.GIVE_FORM);
    new Summit2020();
});

class Summit2020{

    programVision = {
        label: "Vision 2033",
        outreach: "FCBH",
        project_fund_code: "Vision2033-Q",
        project_id: "3394",
        project_title: "Vision 2033",
        textContent: "Your gift will fund the next urgent recording and program needs. Help Faith Comes By Hearing accomplish the task of recording and providing God’s Word in every translated language by 2033."
    }

    programField = {
        label: "Field Programs",
        outreach: "FCBH",
        project_fund_code: "FCBH-N",
        project_id: "182",
        project_title: "Field Programs",
        textContent: "Your gift will send Audio Bibles to communities around the world, allowing them to be discipled in God’s pure Word."
    }

    programBibleRecording = {
        label: "Bible Recordings",
        outreach: "FCBH",
        project_fund_code: "FCBH-UN",
        project_id: "1610",
        project_title: "Bible Recordings",
        textContent: "Your gift will go toward recording God’s Word in another language, giving oral people the chance to hear the Truth and receive Christ."
    }

    amount = document.querySelector('#Chosen-Summit-Amount')

    otherAmount = new Cleave('#Chosen-Summit-Other-Amount', {
        prefix: '$',
        numeral: true,
        numeralThousandsGroupStyle: 'thousand'
    });

    otherAmmountNode = document.querySelector('#Chosen-Summit-Other-Amount')



    constructor() {
        this.otherAmmountNode.disabled = true
        this.amount.addEventListener('change', this.handleAmountChange.bind(this))
        this.captureFormSubmission()
    }

    handleAmountChange(ev){
        this.otherAmmountNode.disabled = ev.target.value !== 'other'
    }

    captureFormSubmission() {
        document.getElementById("chosen-summit-form").addEventListener("submit", this.onGiveSubmit.bind(this));
        document.querySelectorAll(`#chosen-summit-form input[type='submit']`).forEach((button) => {
          button.addEventListener("click", this.onGiveSubmit.bind(this));
        });
    }

    getProgram(giftProgram){
        switch(giftProgram){
            case "Chosen Summit Gift Program Designations Where Most Needed":
                return this.programVision;
            case "Chosen Summit Gift Program Designations Scripture Engagement":
                return this.programField;
                case "Chosen Summit Gift Program Designations Content creation":
                return this.programBibleRecording;
            default: 
                return this.programVision;
        }
    }

    getRegions(){
        const checkedRegions = document.querySelectorAll('input[type="checkbox"]:checked+span')
        let regions = ""
        for (const region of checkedRegions){
            regions+= `- ${region.textContent}\n` 
        }
        if(!regions){
            regions = '- World'
        }
        return `I have a heart for: \n${regions}`
    }

    createFormStructure(amount, program, regions){
        return {
            amounts: {value: amount, usdValue: amount},
            currency_code: "USD",
            program,
            recurring_donation: "no",
            regions
        }
    }

    onGiveSubmit(event) {
        event.preventDefault();
        event.stopPropagation();
        event.stopImmediatePropagation();

        let amount = this.amount.value === 'other' ? this.otherAmount.getRawValue() : this.amount.value
        amount = String(amount).replace("$", '')
        amount = amount || "50000"
        const giftOption = document.querySelector('#Chosen-Summit-Funding-Options').value
        const giftProgram = document.querySelector('input[name="Chosen-Summit-Gift-Program-Designations"]:checked')?.value
        const selectedProgram = this.getProgram(giftProgram)
        const regions = this.getRegions()
        FCBH.saveToStorage(FCBH.GIVE_FORM, this.createFormStructure(amount, selectedProgram, regions));
        if(giftOption==='today'){
            window.location = "/get-involved/give-2";
        }else{
            window.location = `/chosen-summit-2020-6-or-12-months?when=${giftOption}`;
        }

    }
}